<template>
  <div class="about-me">
    <article class="markdown-body" v-html="renderedMarkdown" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      renderedMarkdown: "",
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.getReadme();
    });
  },
  methods: {
    getReadme() {
      this.$gitHubApi.getReadme(this).then((response) => {
        if (response.data) {
          this.renderedMarkdown = this.$marked(response.data);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.about-me {
  width: calc(100% - 260px); // 兼容 IE 10
  flex-grow: 1;
  padding: 30px;
  overflow-y: auto;
}
</style>
